<template>
  <div class="add">
    <div class="blog-info">
      <p class="title">
        <input type="text" placeholder="" v-model="blog.title"/>
      </p>
      <p class="classify">
        <input type="text" placeholder="" v-model="blog.classify"/>
      </p>
      <p class="auth">
        <input type="text" placeholder="" v-model="blog.auth"/>
      </p>
      <p class="brief">
        <textarea type="text" placeholder="" v-model="blog.brief" />
      </p>
      <p class="blog-img">
        <input type="file" multiple="multiple" accept="image/*" @change="uploadImage($event.target.files[0])"/>
        <img v-if="blog.img" :src="blog.img">
      </p>
      <button class="submit" @click="submitBlog"></button>
    </div>
    <mavon-editor
      placeholder=""
      v-model="blog.detail"
      ref="md"
      @imgAdd="addImage"
      @imgDel="delImage"
    ></mavon-editor>
    <button class="submit" @click="submitBlog"></button>
  </div>
</template>

<script>
import { uploadImage } from '@/untils/upload'
import { addArtical } from '@/api/blog'
export default {
  name: 'addBlog',
  data () {
    return {
      blog: {
        title: '',
        img: '',
        auth: '',
        brief: '',
        classify: '',
        detail: ''
      }
    }
  },
  methods: {
    submitBlog () {
      addArtical(this.blog).then(res => {
        console.log(res)
      }).catch(err => {
        console.log(err)
      })
    },
    addImage (pos, file) {
      uploadImage(file, 'blog_content_image', promise => {
        promise.then(res => {
          this.$refs.md.$img2Url(pos, res.data.path)
        }).catch(err => {
          console.log(err)
        })
      })
    },
    delImage (pos) {
      console.log(pos)
    },
    uploadImage (img) {
      uploadImage(img, 'blog_img', promise => {
        promise.then(res => {
          this.blog.img = res.data.path
        }).catch(err => {
          console.log(err)
        })
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import './addBlog.less';
</style>
