import Vue from 'vue'
import axios from 'axios'

const axiosX = axios.create({
  baseURL: process.env.VUE_APP_API,
  timeout: 3000
})

axiosX.interceptors.request.use(function (config) {
  console.log(config)
  return config
}, function (err) {
  return Promise.reject(err)
})

axiosX.interceptors.response.use(function (response) {
  let res
  if (response && (Number(response.status) === 200 || Number(response.status) === 304)) {
    res = response.data
  }
  if (res) {
    if (res.success) {
      return res
    } else {
      return Promise.reject(res)
    }
  }
}, function (err) {
  console.log('errr', err)
  if (err && err.response) {
    switch (err.response.status) {
      case 400:
        err.message = ''
        break
      case 401:
        err.message = ''
        break
      case 403:
        err.message = ''
        break
      case 404:
        err.message = ','
        break
      case 405:
        err.message = ''
        break
      case 408:
        err.message = ''
        break
      case 500:
        err.message = ''
        break
      case 501:
        err.message = ''
        break
      case 502:
        err.message = ''
        break
      case 503:
        err.message = ''
        break
      case 504:
        err.message = ''
        break
      case 505:
        err.message = 'http'
        break
      default:
        err.message = `${err.response.status}`
    }
  } else {
    err.message = ''
    window.sessionStorage.clear()
  }
  return Promise.reject(err)
})

const Post = (url='/', data={}) => {
  return axiosX.post(url, data)
}
const Get = (url='/', data={}) => {
  return axiosX.get(url, data)
}

Vue.$Post = Post
Vue.$Get = Get

export {Post, Get}
